  transition: all 0.6s cubic-bezier(0.8, -0.4, 0.2, 1.7);
  transform-style: preserve-3d;
  &:hover .social-icon {
    opacity: 1;
    top: 0;
  }
}
.outer-div:hover .inner-div {
  transform: rotateY(180deg);
}
