.back {
  transform: rotateY(180deg);
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: var(--black);
  border-radius: 5px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.social-media-wrapper {
  font-size: 36px;
  .social-icon {
    position: relative;
    top: 20px;
    margin-left: 10px;
    margin-right: 10px;
    opacity: 0;
    color: #fff;
    transition: all 0.4s cubic-bezier(0.3, 0.7, 0.1, 1.9);
  }
  .social-icon:nth-child(1) {
    transition-delay: 0.6s;
  }
  .social-icon:nth-child(2) {
    transition-delay: 0.7s;
  }
  .social-icon:nth-child(3) {
    transition-delay: 0.8s;
  }
  .social-icon:nth-child(4) {
    transition-delay: 0.9s;
  }
}
.fab {
  position: relative;
  top: 0;
  left: 0;
  transition: all 200ms ease-in-out;
}
.fab:hover {
  top: -5px;
}
@keyframes nutrinos-glow {
  0%,
  100% {
    box-shadow: 0 0 10px rgba(34, 197, 94, 0.6), 0 0 20px rgba(34, 197, 94, 0.4);
  }
  50% {
    box-shadow: 0 0 20px rgba(34, 197, 94, 0.8), 0 0 30px rgba(34, 197, 94, 0.6);
  }
}
@keyframes nutrinos-glow-reduced {
  0%,
  100% {
    box-shadow: 0 0 3px rgba(34, 197, 94, 0.2), 0 0 6px rgba(34, 197, 94, 0.1);
  }
  50% {
    box-shadow: 0 0 5px rgba(34, 197, 94, 0.25), 0 0 8px rgba(34, 197, 94, 0.15);
  }
}
@keyframes lightning-pulse {
  0%,
  100% {
    transform: scale(1) rotate(0deg);
  }
  25% {
    transform: scale(1.1) rotate(-5deg);
  }
  50% {
    transform: scale(1.2) rotate(5deg);
  }
  75% {
    transform: scale(1.1) rotate(-3deg);
  }
}
@keyframes role-shimmer {
  0% {
    background-position: -200% 0;
  }
  100% {
    background-position: 200% 0;
  }
}
.nutrinos-badge {
  animation: nutrinos-glow 2s ease-in-out infinite;
}
.nutrinos-badge-reduced {
  animation: nutrinos-glow-reduced 2s ease-in-out infinite;
}
.lightning-icon {
  animation: lightning-pulse 3s ease-in-out infinite;
}
.role-badge {
  background-size: 200% 100%;
  animation: role-shimmer 3s ease-in-out infinite;
}
@keyframes fade-in {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes slide-up {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.animate-fade-in {
  animation: fade-in 0.8s ease-out;
}
.animate-slide-up {
  animation: slide-up 0.6s ease-out;
}
.resource-card {
  transition: transform 180ms ease, box-shadow 180ms ease;
  border-radius: 0.5rem;
  border: 1px solid rgba(15, 23, 42, 0.06);
  background-clip: padding-box;
}
.resource-card:hover {
  transform: translateY(-3px);
  box-shadow: 0 6px 18px rgba(2, 6, 23, 0.06);
}
@keyframes shimmer {
  0% {
    background-position: -1000px 0;
  }
  100% {
    background-position: 1000px 0;
  }
}
.bg-animate-shimmer {
  background: linear-gradient(90deg, #f3f4f6 25%, #e5e7eb 50%, #f3f4f6 75%);
  background-size: 1000px 100%;
  animation: shimmer 2s infinite linear;
}
